import { BuildContext, Component, HtmlNode, StatelessComponent } from "@equantic/runtime";
import { List } from "./List";

export class Heading extends Component {
    level: number;
    content: string;
    render() {
        return { tag: `h${Math.min(Math.max(this.level, 1), 6)}`, attributes: this.buildAttributes(), events: this.buildEvents(), children: [this.htmlNode.text(this.content)] };
    }

}

