import { BuildContext, Component, HtmlNode, StatelessComponent } from "@equantic/runtime";
import { List } from "./List";

export class Text extends Component {
    content: string;
    paragraph: boolean;
    render() {
        return { tag: this.paragraph ? 'p' : 'span', attributes: this.buildAttributes(), events: this.buildEvents(), children: [this.htmlNode.text(this.content)] };
    }

}

