import { BuildContext, Component, HtmlNode, StatelessComponent } from "@equantic/runtime";

export class Checkbox extends Component {
    checked: boolean;
    onChange: (bool: boolean) => void;
    disabled: boolean;
    render() {
        let inputAttrs = this.buildAttributes();inputAttrs["type"] = 'checkbox';if (this.checked) inputAttrs["checked"] = 'true';if (this.disabled) inputAttrs["disabled"] = 'true';let events = this.buildEvents();return { tag: 'input', attributes: inputAttrs, events: events };
    }

}

